<template>
    <div class="table-container">
        <bk-table
            :max-height="maxHeight"
            :data="data"
            ref="table"
            :outer-border="false"
            :header-border="false"
            @select="onselect"
            @select-all="selectAll"
            @selection-change="selectChange"
            :pagination="pagination"
            @page-change="handlePageChange"
            @page-limit-change="limitChange"
            @filter-change="filterChange"
            @sort-change="sortChange"
        >
            <bk-table-column v-if="selectColumn.visible" type="selection" width="60" :fixed="selectColumn.fixed"></bk-table-column>
            <bk-table-column v-if="orderColumn.visible" type="index" label="" width="60" :fixed="orderColumn.fixed"></bk-table-column>
            <bk-table-column
                v-for="item in setting.selectFields"
                :key="item.key"
                :width="item.width"
                :label="item.label"
                v-bind="item.attr"
                :align="item.align || 'left'"
                :prop="item.key"
                :column-key="item.key"
                :sortable="item.sortable"
                :filters="item.filters"
                :filter-method="columnFilterMethod"
                :filter-multiple="item.filterMultiple">
                <template slot-scope="props">
                    <template v-if="item.scopedSlots">
                        <slot :name="item.scopedSlots.customRender" :scope="props.row"></slot>
                    </template>
                    <span v-else>{{ props.row[item.key] || (props.row[item.key] === 0 ? props.row[item.key] : '--')}}</span>
                </template>
            </bk-table-column>
            <bk-table-column type="setting" v-if="settingsFields && settingsFields.length > 0">
                <bk-table-setting-content
                    :fields="setting.fields"
                    :selected="setting.selectFields"
                    :max="setting.max"
                    value-key="key"
                    :size="setting.size"
                    @setting-change="handleSettingChange">
                </bk-table-setting-content>
            </bk-table-column>
        </bk-table>
    </div>
</template>

<script>
    /**
     * @comopnent 
     * @props {
     *   columns:[]  :scopedSlots: { customRender: 'name' }
     *   data:
     *   pagination: { current:,count:,limit: }
     *   selectColumn:{visible:true() , fixed:true() }
     *   orderColumn:{visible:true() , fixed:true() }
     *   settingsFields:[]
     * }
     */
    export default {
        name: 'table-component',
        props: {
            data: {
                type: Array,
                default: () => [],
                require: true
            },
            columns: {
                type: Array,
                default: () => [],
                require: true
            },
            pagination: {
                type: Object,
                default: () => {}
            },
            settingsFields: {
                type: Array,
                default: () => []
            },
            selectColumn: {
                type: Object,
                default: () => {
                    return {
                        visible: false,
                        fixed: false
                    }
                }
            },
            orderColumn: {
                type: Object,
                default: () => {
                    return {
                        visible: false,
                        fixed: false
                    }
                }
            }
        },
        data() {
            return {
                setting: {
                    fields: [],
                    selectFields: [],
                    size: 'small'
                },
                topDistance: 0,
                maxHeight: 'auto'
            }
        },
        mounted() {
            this.setting.selectFields = this.columns
            this.setting.fields = this.settingsFields || []
            // 
            // offsetTop
            this.topDistance = parseInt(this.$refs.table.$el.offsetTop)
            this.maxHeight = window.innerHeight - this.topDistance - 52 - 20
            window.onresize = () => {
                return (() => {
                    this.settingPosition()
                    this.maxHeight = window.innerHeight - this.topDistance - 52 - 20
                })()
            }
        },
        methods: {
            // 
            columnFilterMethod(value, row, column) {
                const property = column.property
                return row[property] === value
            },
            //  Checkbox 
            onselect(selection, row) {
                this.$emit('select', selection, row)
            },
            //  Checkbox 
            selectAll(selection) {
                this.$emit('select-all', selection)
            },
            // 
            selectChange(selection) {
                this.$emit('selection-change', selection)
            },
            // 
            handlePageChange(page) {
                this.$emit('page-change', page)
            },
            // 
            limitChange(limit) {
                this.$emit('page-limit-change', limit)
            },
            handleSettingChange({ fields, size }) {
                this.setting.size = size
                const newsFields = []
                const selectedFields = this.columns
                fields.forEach(item => {
                    const currentIndex = selectedFields.find(field => field.key === item.key)
                    if (currentIndex) {
                        newsFields.push(currentIndex)
                        return
                    }
                    newsFields.push(item)
                })
                // 
                const [first] = [...selectedFields].reverse()
                if (first.key === 'operation') {
                    newsFields.push(first)
                }
                this.setting.selectFields = [...newsFields]
                this.setting.selectFields = [...this.setting.selectFields]
                // 
                const adnvancedWidget = this.setting.selectFields.map(item => {
                    return {
                        name: item.key,
                        label: item.label,
                        ...item.conditionAttr
                    }
                })
                this.$emit('handleSettingChange', adnvancedWidget, this.setting.selectFields)
            },
            // 
            filterChange(key) {
                this.$emit('filter-change', key)
            },
            // 
            sortChange({ column, prop, order }) {
                this.$emit('sort-change', { column, prop, order })
            }
        }
    }
</script>

<style scoped lang="scss">
.table-container {
    background: #fff;
    border: 1px solid #dfe0e5;
}
/deep/ .bk-table::before {
    height: 0;
}
/deep/ .setting-title {
    text-align: right;
    > label {
        float: left !important;
    }
}
/deep/.bk-table-scrollable-x {
    .bk-table-body-wrapper {
        &::-webkit-scrollbar {
            /**/
            width: 10px;  /**/
            height: 8px;
            border: none;
        }
        &::-webkit-scrollbar-thumb {
            /**/
            border-radius: 10px;
            box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
            background: #D2D2D2;
        }
        &::-webkit-scrollbar-track {
            /**/
            border-radius: 15px;
            background: #ededed;
        }
    }
}
.bk-table-setting-content {
    /deep/ .content-title {
        padding-bottom: 5px;
        margin-bottom: 10px;
        border-bottom: 1px solid #eee;
    }
    /deep/ .content-line-height {
        display: none;
    }
    /deep/ .content-options {
        button {
            border-radius: 2px;
            min-width: 60px;
            letter-spacing: 2px;
        }
    }
}
/deep/ .bk-table-fixed {
    th {
        border: none !important;
    }
}
/deep/ .bk-table::before {
    height: 0;
}
/deep/ .bk-table-body {
    tr:last-child {
        td {
            //border-bottom: 0px;
        }
    }
}
/deep/ .bk-table-column-setting {
    border-left: 0;
}
</style>
